@import "styles/variables";
@import "styles/mixins";

.label {
  @include tableCell($colorRegular, 400);

  position: relative;
  display: flex;
  align-items: center;
  width: max-content;
  height: 2.8rem;
  cursor: pointer;

  &::before {
    content: "";
    position: absolute;
    top: calc(50% - 1.4rem / 2);
    left: 0;
    width: 3rem;
    height: 1.4rem;
    background-color: $colorBorder;
    border-radius: 0.7rem;
    transition-duration: $transitionDuration;
    transition-timing-function: ease-out;
  }

  &::after {
    content: "";
    position: absolute;
    top: calc(50% - 2rem / 2);
    left: 0;
    width: 2rem;
    height: 2rem;
    background-color: $colorBgLightest;
    border-radius: 50%;
    box-shadow: 0 0.1rem 0.2rem 0 rgba(0, 0, 0, 0.28);
    transition-duration: $transitionDuration;
    transform: translateX(0);
  }

  &:hover::before {
    background-color: $colorBgSidebar;
  }
}

.labelChecked {
  &::before {
    background-color: $colorHighlightDark;
  }

  &::after {
    transform: translateX(1rem);
  }
}

.labelContent {
  padding-left: 4.5rem;
}

.labelDisabled {
  pointer-events: none;
  opacity: 0.5;
}
