@import "styles/variables";
@import "styles/mixins";

.item {
  &:first-child {
    .trigger::before {
      left: -1px;
    }
  }

  &:last-child {
    .trigger::before {
      left: 1px;
    }
  }
}

.trigger {
  @include resetButton;
  @include regular;

  position: relative;
  padding: 1.3rem 2rem;
  transition-duration: $transitionDuration;

  &::before {
    content: "";
    position: absolute;
    top: -1px;
    left: 0;
    width: 100%;
    height: calc(100% + 2px);
    background-color: $colorHighlightDark;
    border-radius: 2.25rem;
    z-index: -1;
    opacity: 0;
    transition-duration: $transitionDuration;
  }
}

.triggerActive {
  color: $colorLightest;

  &::before {
    opacity: 1;
  }
}

.itemSimplified {
  &:first-child,
  &:last-child {
    .trigger::before {
      left: 0;
    }
  }

  .trigger {
    padding: 0 0.5rem 0.4rem 0.5rem;


    &::before {
      content: "";
      position: absolute;
      top: auto;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: $colorHighlightDark;
      z-index: 1;
      opacity: 0;
      transition-duration: $transitionDuration;
    }
  }

  .triggerActive {
    color: $colorHighlightDark;

    &::before {
      opacity: 1;
    }
  }
}
