@import "styles/mixins";
@import "styles/variables";

.wrapper {
  padding-bottom: 2rem;
  border-bottom: 1px solid $colorBorder;
}

.list {
  @include tableCell($colorRegular, 400);

  padding: 0;
}

.item {
  margin-top: 1.6rem;
}

.heading {
  margin-top: 0.2rem;
}

.description {
  @include tableCell($colorRegular, 400);

  margin-top: 1.6rem;
  line-height: 2.4rem;
}

.controls {
  display: flex;
  align-items: center;
  margin-top: 3rem;
}

.status {
  @include tableCell($colorRegularLightest, 500);

  display: flex;
  align-items: center;
}

.icon {
  margin-left: 1rem;
  width: 2rem;
  fill: $colorRegularLightest;
}

.button {
  @include tableCell($colorHighlightDark, 500);
}

.social {
  margin-left: auto;
}
