@import "styles/mixins";

.list {
  @include resetList;

  display: flex;
  align-items: center;
  border: 1px solid $colorRegularLightest;
  border-radius: 2.25rem;
}

.listSimplified {
  border: none;
  border-radius: 0;

  .item {
    &:not(:first-child) {
      margin-left: 2rem;
    }
  }
}
