@import "styles/variables";
@import "styles/mixins";

.wrapper {
  display: grid;
  grid-template-columns: 12rem auto auto;
  grid-gap: 1rem;
  align-items: center;
}

.label {
  @include tableCell($colorRegular, 400);

  &:first-child {
    padding-left: 2rem;
  }

  &:last-child {
    padding-left: 1rem;
  }
}

.bar {
  height: 1.2rem;
  background-color: $colorHighlightDark2;
  border-radius: 0.6rem;
  opacity: 0.7;
}
