@import "styles/variables";
@import "styles/mixins";

.wrapper {
  display: grid;
  grid-template-columns: 1fr 33rem;
  grid-column-gap: 2rem;
  padding-bottom: 5rem;
  align-items: flex-start;
}

.popular {
  grid-row: 1 / 3;
  grid-column: 2 / 3;
}

.link {
  @include tableCell($colorHighlightDark, 500);
}
