@import "styles/mixins";

.wrapper {
  display: flex;
  flex-direction: column;

  .image {
    width: 100%;
    height: 21rem;
    object-fit: cover;

    & + .content > .author {
      margin-top: 0.8rem;
    }
  }

  .heading {
    margin-top: 1rem;

    & + .description {
      margin-top: 0.8rem;
    }
  }

  .description {
    margin-top: 1rem;
  }

  .footer {
    display: flex;
    flex-direction: column;
  }

  .tags {
    margin-top: 0.5rem;
  }

  .social {
    margin-top: 1rem;
    margin-left: auto;
  }
}

.wrapperBlog {
  .image {
    height: 16.4rem;
  }
}

.wrapperHorizontal {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(6, auto);
  grid-column-gap: 3rem;

  .image {
    grid-column: 1 / 2;
    grid-row: 1 / -1;

    & + .content > .author {
      margin-top: 0;
    }
  }

  & > *:not(.image) {
    grid-column: 2 / 3;
  }

  .heading {
    @include h1;

    margin-top: 0.2rem;
  }
}
