.template {
  grid-gap: 0;
}

.wrapper {
  width: 93rem;
}

.content {
  padding: 3.2rem 12rem;
}

.form {
}

.fieldset {
  grid-column: 1 / -1;
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  grid-gap: 1.6rem 3rem;
  margin-top: 2.4rem;
  padding: 0;
  border: none;
}

.title,
.tags {
  grid-column: 1 / 3;
}

.inputImage {
  grid-column: 3 / 4;
  grid-row: 2 / 3;
}

.image {
  grid-column: 1 / -1;
  width: 100%;
  height: 37rem;
  object-fit: cover;
}

.editor {
  grid-column: 1 / -1;
  padding-top: 0.7rem;
}
