@import "styles/variables";
@import "styles/mixins";

.wrapper {
  align-self: flex-start;
  padding: 2rem;
  border: 1px solid $colorBorder;

  .heading {
    text-transform: uppercase;
  }

  .list {
    @include resetList;

    margin-top: 3rem;
  }

  .item {
    &:not(:last-child) {
      margin-bottom: 1rem;
      padding-bottom: 1rem;
      border-bottom: 1px solid $colorBorder;
    }
  }
}
