@import "styles/variables";
@import "styles/mixins";

.wrapper {
  width: 100%;
  min-height: 100vh;
  background-color: $colorHighlightDark3;

  .content {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
    padding-top: 13rem;
    width: 28rem;
  }

  .description {
    @include h1(700, $colorLightest);

    margin: 5.8rem 0 0 0;
    text-align: center;
  }

  .icon {
    width: 16.4rem;
    fill: $colorLightest;
    opacity: 0.7;
  }

  .form {
    margin-top: 1.6rem;
    width: 100%;
  }

  .input {
    width: 100%;

    &:not(:first-child) {
      margin-top: 1rem;
    }
  }

  .button {
    display: block;
    margin: 3.2rem auto 0 auto;
    width: 21rem;
    font-weight: 500;
    color: $colorLightest;
    border: 2px solid $colorLightest;

    svg {
      fill: $colorLightest;
    }
  }
}
